@import url(reset.css);
.link{
    text-decoration: none;
    color:black;
}
.header_bar{
    display:flex;
    justify-content: space-around;
    position: relative;
}

.header_menu{
    background-color: white;
    width: 100%;
    position: fixed;
    top:0;
    z-index: 1;
    height: 50px;
}
.header_menu > ul{
    z-index:2;
    text-align: center;
}
.header_menu >ul> li{
    display:inline-block;
    align-content: center;
    margin-right:40px;
    padding-top:15px;
}

.header_bar >h1{
    margin-left:20px;
    font-weight: bold;
    position:absolute;
    top:70px;
    font-size:24px;
}
.hidden{
    display:none;
}
@media screen and (max-width:767px){
    .hidden{
        display:block;
    }
    .menu_list{
        width: 100%;
        height: 300px;
        background-color: white;
        clear: both;
        text-align: center;
        z-index: 2;
    }
    .menu_list > li{
        font-size:20px;
        margin-bottom:30px;
        
    }
    .header_menu{
        display:none;
    }
    
    .header_bar h1{
        top:13px;
        font-size:20px;
    }
    
    .btn_box > button{
        background-color: white;
        border:none;
        font-size: 20px;
    }
}