.container{
   width: 100%;
}

.container__box__img{
    width: 100%;  
    height: auto;
}
.img__section{
    width: 1250px;
    margin:0 auto;

}
.img__section__box{    
    margin-top:30px;
    display: grid;
    grid-template-columns: repeat(3, 400px);
    justify-items: center;
} 
.img__section__box img{
    width: 297px; height: 222px;
    margin-top:20px;
    
    cursor: pointer;
}


.golf_text{
    border:3px solid black;
    width: 420px;
    height: 500px;
    margin:0 auto;
    display:flex;
    align-items: center;
    justify-content: center;
}

.golf_text p{
    text-align: center;
}
.golf_text p span{
    display:block;
    margin-top:15px;
}
.price{
    color:red;
}
.img__section__box img:nth-child(1):hover{
    content:url(../img/flower/1-1.png)
}
@media screen and (max-width:767px){
    .golf_text{
        width: 95%;
    }
    
    .container__box{
        width: 100%;
        height: auto;
    }
    .img__section{
        width: auto;
    }
   .img__section__box >img{
       width: 80vw;
       display:block;
       margin:0 auto;
        margin-bottom:20px;
   }
   .img__section__box{
       display:block;
    
   }
}